@import url('https://api.mapbox.com/mapbox-gl-js/v1.12.0/mapbox-gl.css');

h1,
p {
  font-family: Lato;
}

.map-wrapper {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  border-radius: inherit;
}

#map {
  width: 100%;
  height: 100%;
}

.mapboxgl-ctrl-top-right {
  max-height: calc(100vh - 40px);
  overflow-y: auto;
  pointer-events: all;
}
